.contacts-block__title, .about__title h2, .services__title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 10px;
  height: 40px;
  box-shadow: 0px 0px 5px 2px #2E8B57;
  text-transform: uppercase;
  transition: all 0.5s;
}

.contacts-block__title:hover, .about__title h2:hover, .services__title:hover {
  background-color: #c4f5cc;
}

.border-button {
  text-decoration: none;
  display: inline-block;
  padding: 8px;
  margin: auto 10px;
  position: relative;
  color: #c4f5cc;
  border: 1px hidden;
  background: none;
  font-weight: 700;
  font-family: "Jura", sans-serif;
  line-height: 22px;
  letter-spacing: 2px;
}

.border-button:before, .border-button:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
  box-sizing: border-box;
}

.border-button:before {
  bottom: 0;
  left: 0;
  border-left: 1px solid #c4f5cc;
  border-top: 1px solid #c4f5cc;
  transition: 0s ease opacity 0.8s, 0.2s ease width 0.4s, 0.2s ease height 0.6s;
}

.border-button:after {
  top: 0;
  right: 0;
  border-right: 1px solid #c4f5cc;
  border-bottom: 1px solid #c4f5cc;
  transition: 0s ease opacity 0.4s, 0.2s ease width, 0.2s ease height 0.2s;
}

.border-button:hover:before,
.border-button:hover:after {
  height: 100%;
  width: 100%;
  opacity: 1;
}

.border-button:hover:before {
  transition: 0s ease opacity 0s, 0.2s ease height, 0.2s ease width 0.2s;
}

.border-button:hover:after {
  transition: 0s ease opacity 0.4s, 0.2s ease height 0.4s, 0.2s ease width 0.6s;
}

.border-button:hover {
  background: rgba(255, 255, 255, 0.2);
}

@font-face {
  font-family: "Caveat";
  src: url("../fonts/Caveat-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  font-family: "Roboto";
  min-width: 300px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  position: fixed;
  z-index: 15;
  top: 0;
  width: 100%;
  transition: top 0.5s;
  background-color: #0b0b0b;
}

.navbar {
  display: flex;
  justify-content: space-between;
  height: 80px;
  max-width: 95vw;
  position: relative;
  z-index: 12;
}
.navbar__logo-img {
  height: 80px;
}
.navbar__button {
  display: none;
}

.menu-list {
  display: flex;
  justify-content: end;
  align-items: center;
  transition: all 0.5s;
}
.menu-list__item {
  text-transform: uppercase;
  font-family: "Jura";
  font-size: 18px;
  font-weight: 400;
}
.menu-list__item-link {
  color: #c4f5cc;
}

.navbar-btn {
  display: none;
  margin: auto 0;
  height: 50px;
  padding: 10px;
  cursor: pointer;
  background-color: transparent;
}

.navbar-btn:hover {
  background-color: rgba(34, 45, 44, 0.5);
}

.navbar-btn span {
  display: block;
  width: 30px;
  height: 4px;
  margin: 4px;
}

.services__title {
  margin-top: 100px;
  margin-bottom: 0px;
}

.services-block {
  padding: 10px 0px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}
.services-block__item {
  box-shadow: 0px 0px 5px 2px #2E8B57;
  border: 1px cadetblue solid;
  border-radius: 10px;
  background-color: cadetblue;
  position: relative;
  cursor: pointer;
}
.services-block__item:hover .services-block__title {
  opacity: 1;
}
.services-block__item:hover .services-block__tel {
  opacity: 1;
}
.services-block__item:hover .services-block__image {
  opacity: 0.3;
}
.services-block__image {
  transition: all 1s;
  border-radius: 10px;
}
.services-block__title {
  display: block;
  padding: 10px;
  position: absolute;
  right: 0;
  left: 0;
  z-index: 9;
  text-align: center;
  opacity: 0;
  color: #FFDEAD;
  align-items: center;
  transform: translateY(-50%);
  transition: all 1s;
  background: rgba(34, 45, 44, 0.7);
}
.services-block__title {
  top: 30%;
}
.services-block__tel {
  font-size: 14px;
  letter-spacing: 0px;
  display: block;
  opacity: 0;
  padding: 6px;
  position: absolute;
  z-index: 9;
  text-align: center;
  transform: translateY(-50%);
  left: 0;
  right: 0;
  top: 80%;
  width: max-content;
  margin: 0 auto;
}
.services-block__tel:hover {
  transition: all 0.5s;
  box-shadow: 0px 0px 5px 2px #222d2c;
  background: rgba(34, 45, 44, 0.9);
}

.about {
  background-color: #D3D3D3;
}

.about__title {
  padding-top: 15px;
  margin: 25px auto 10px;
}

.about__title h2 {
  background: #fff;
}

.about-block {
  display: grid;
  align-items: center;
  grid-template-columns: 5fr 3fr;
  font-family: "Caveat";
  font-size: 20px;
  line-height: 30px;
  grid-auto-columns: max-content;
  z-index: 10;
  width: 100%;
  padding-bottom: 15px;
}
.about-block__text {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
.about-block__text-item {
  position: relative;
  z-index: 12;
  box-shadow: 0px 0px 5px 2px #2E8B57;
  border-radius: 10px;
  opacity: 0;
  padding: 13px;
  cursor: pointer;
  height: 56px;
  transition: all 0.5s;
}
.about-block__diplom {
  margin-left: 10px;
  position: relative;
  z-index: 12;
  transform: translateX(100%);
  opacity: 0;
  height: 320px;
  width: 460px;
}
.about-block__diplom img {
  object-fit: cover;
  object-position: left 50%;
  box-shadow: 0px 0px 5px 2px #2E8B57;
  border-radius: 10px;
  height: 320px;
  width: 460px;
}

.animated .about-block__text-item {
  animation: 1.5s toright ease-in-out forwards;
}
.animated .about-block__text-item:nth-child(2) {
  animation-delay: 0.3s;
}
.animated .about-block__text-item:nth-child(3) {
  animation-delay: 0.6s;
}
.animated .about-block__text-item:nth-child(4) {
  animation-delay: 0.9s;
}
.animated .about-block__text-item:nth-child(5) {
  animation-delay: 1.2s;
}
.animated .about-block__diplom {
  animation: 2.8s toleft ease-in-out forwards;
}
@keyframes toleft {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes toright {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0px);
    opacity: 1;
    background: #c4f5cc;
  }
}

.contacts-block {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 10px;
}
.contacts-block__title {
  margin: 25px auto 10px;
}
.contacts-block__address {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0px 0px 5px 2px #2E8B57;
  margin-bottom: 10px;
  background-color: #c4f5cc;
  font-family: "Caveat";
  font-size: 22px;
  cursor: pointer;
}
.contacts-block__onmap iframe {
  width: 100%;
  height: 300px;
  box-shadow: 0px 0px 5px 2px #2E8B57;
  border-radius: 10px;
}

.footer {
  background-color: #0b0b0b;
}
.footer .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto;
}

.footer-contacts {
  font-family: "Jura";
}
.footer-contacts__list-item {
  margin: 10px 20px;
  font-size: 16px;
  line-height: 24px;
}
.footer-contacts__list-item a {
  color: #c4f5cc;
}

.footer-social__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: end;
}
.footer-social__list-item {
  font-size: 32px;
  line-height: 32px;
  margin: 0px;
}
.footer-social__list-item a {
  color: #c4f5cc;
}

@media (max-width: 992px) {
  .about-block {
    font-size: 17px;
    line-height: 20px;
    padding-bottom: 15px;
  }
  .about-block__text-item {
    padding: 8px;
  }
}
@media (max-width: 840px) {
  .services-block {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 768px) {
  .navbar {
    position: relative;
  }
  .menu-list {
    border-radius: 0 0 10px 10px;
    display: table-column;
    background: #0b0b0b;
    position: absolute;
    z-index: -1;
    right: 0;
    top: -250px;
  }
  .menu-list__item {
    margin: 10px 55px 10px 20px;
  }
  .menu-list__item-link {
    margin: 0 auto;
  }
  .menu-list.active {
    top: 80px;
  }
  .navbar-btn {
    position: relative;
    display: block;
    margin: auto 0;
    height: 57px;
    padding: 10px;
    cursor: pointer;
  }
  .navbar-btn span {
    display: block;
    width: 30px;
    height: 4px;
    background-color: #c4f5cc;
    margin: 6px 3px;
  }
  .navbar-btn.active span {
    transition: all 0.5s;
  }
  .navbar-btn.active span:nth-child(1) {
    position: absolute;
    top: 21px;
    transform: rotate(45deg);
  }
  .navbar-btn.active span:nth-child(2) {
    opacity: 0;
  }
  .navbar-btn.active span:nth-child(3) {
    position: absolute;
    bottom: 21px;
    transform: rotate(-45deg);
  }
  .about-block {
    grid-template-columns: 1fr;
    font-size: 20px;
    line-height: 26px;
    padding-bottom: 15px;
  }
  .about-block__text-item {
    padding: 15px;
  }
  .about-block__diplom {
    margin-top: 13px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 650px) {
  .navbar {
    height: 60px;
  }
  .navbar__logo-img {
    height: 60px;
  }
  .menu-list.active {
    top: 60px;
  }
  .contacts-block {
    grid-template-columns: 1fr;
  }
  .about-block {
    grid-template-columns: 1fr;
  }
  .about-block__text-item {
    height: unset;
  }
  .services__title {
    margin-top: 80px;
  }
}
@media (max-width: 540px) {
  .about-block__diplom {
    width: 100%;
    height: unset;
  }
  .about-block__diplom img {
    width: 100%;
    height: 100%;
  }
  .services-block {
    grid-template-columns: 1fr;
  }
}